.description {
  display: -ms-flexbox;
  display: flex;

  -ms-flex-pack: justify;

      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  padding: 20px 20px 20px 20px;
  margin-bottom: 50px;
}

.imagine {
  width: 80vw;
  height: auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  margin-right: 20px;
  border-radius: 15px;
  -webkit-box-shadow: 0px 10px 15px rgb(87, 87, 87), 0 10px 20px rgb(102, 102, 102);
          box-shadow: 0px 10px 15px rgb(87, 87, 87), 0 10px 20px rgb(102, 102, 102);
}

.img-wd {
  max-width: 100%;
  max-height: 100%;
  border-radius: 15px;
}

.text {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  width: 100%;
  text-align: justify;
}

.text h1 {
  font-size: 2.5em;
}

.text p {
  font-size: 1.2em;
}

.download {
  margin-top: 2.5em;
  -ms-flex-item-align: center;
      align-self: center;

  /* padding: 30px; */
}

.example_d {
  cursor: pointer;
  color: #20bf6b !important;
  font-weight: 1000;
  text-transform: uppercase;
  background: #eae7dc;
  padding: 20px 50px;
  border: 4px solid #20bf6b !important;
  border-radius: 6px;
  display: inline-block;
  -webkit-transition: all 0.3s ease 0s;
  -o-transition: all 0.3s ease 0s;
  transition: all 0.3s ease 0s;
}

.example_d:hover {
  color: #fff !important;
  border-radius: 50px;
  border-color: #494949 !important;
  -webkit-transition: all 0.3s ease 0s;
  -o-transition: all 0.3s ease 0s;
  transition: all 0.3s ease 0s;
  background-color: #32adff;
}

.color {
  color: #20bf6b;
}

.color:hover {
  color: #494949;
}

@media screen and (max-width: 768px) {
  .description {
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
  }

  .imagine {
    margin-bottom: 20px;
  }
}
  


@media (max-width: 600px) {
  .description {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
  }

  .imagine {
    padding: 0;
    margin: 0;
    width: 330px;
    height: auto;
  }

  .img-wd{
      max-width: 100%;
      max-height: 100%;
  }

  .text {
    padding-top: 30px;
  }

  .text h1 {
    font-size: 1.5em;
    text-align: center;
  }

  .text p {
    font-size: 1em;
    text-align: justify;
  }
}



