.bg {
  background-image: url("../../assets/Aerial-view-of-Bucharest.jpg");
  background-attachment: fixed;
  /* Center and scale the image nicely */
  background-position: top;
  background-repeat: no-repeat;
  background-size: cover;
  -o-object-fit: contain;
     object-fit: contain;
}

.layer {
  height: 100%;
  width: 100%;
  background: rgba(0, 0, 0, 0.6);
  top: 0;
  left: 0;
}

p {
  margin: 0;
  padding-top: 20px;
}

.shoucase {
  opacity: 1;
  height: 100vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.nume {
  color: white;
  font-size: 4rem;
  font-weight: 900;
  padding-bottom:3rem;
}

/* .nume span {
  color: #fffb01;
} */

.descriere {
  color: white;
  font-size: 1.5em !important;
  display: -ms-flexbox;
  display: flex;
}

.icons {
  display: -ms-flexbox;
  display: flex;
  height: 10%;
  width: 40%;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.icons a {
  text-decoration: none;
  color: white;
}

.ceva {
  margin: 0;
  padding: 0;
}

.ceva {
  font-size: 1.5em;
  border-right: 0.05em solid;
  -webkit-animation: caret 1s steps(1) infinite;
          animation: caret 1s steps(1) infinite;
  /* align-self: flex-start; */
}
@-webkit-keyframes caret {
  50% {
    border-color: transparent;
  }
}
@keyframes caret {
  50% {
    border-color: transparent;
  }
}

@media (max-width: 600px) {
  .nume {
    font-size: 50px;
  }

  .icons {
    width: 50%;
    padding-top: 10px;
  }

  .icons a {
    font-size: 10px;
  }

  .descriere {
    margin-top: 10px;
    font-size: 1rem !important;
    text-align: center;
  }

  .ceva {
    display: none;
  }
}



