.navbar {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-align: center;
      align-items: center;
  background-color: transparent;
  z-index: 10;
  font-weight: 600;
  position: fixed;
  top: 0;
  width: 100vw;
  -webkit-transition: ease 0.3s;
  -o-transition: ease 0.3s;
  transition: ease 0.3s;
  /* box-shadow: 0 2px 2px -2px rgba(0, 0, 0, 0.2); */
}

.navbar2 {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-align: center;
      align-items: center;
  background-color: #333;
  z-index: 10;
  font-weight: 600;
  position: fixed;
  top: 0;
  width: 100vw;
  -webkit-transition: ease 0.3s;
  -o-transition: ease 0.3s;
  transition: ease 0.3s;
  -webkit-box-shadow: 0px -2px 10px rgb(87, 87, 87), 0 -2px 15px rgb(102, 102, 102);
          box-shadow: 0px -2px 10px rgb(87, 87, 87), 0 -2px 15px rgb(102, 102, 102);
}

.navbar a {
  text-decoration: none;
  color: white;
}

.navbar2 a {
  text-decoration: none;
  color: white;
}

.navbarTitle {
  /* margin-right: auto; */
  font-size: 150%;
  padding: 12px 16px;
}

.navbarItem {
  padding: 16px 16px;
  font-size: 1.5em;
  cursor: pointer;
  vertical-align: middle;
}

.navbar-bg {
  background-color: black;
}

@media (max-width: 600px) {
  .navbarItem {
    font-size: 20px;
  }
}



