.work {
  /* background: -webkit-linear-gradient(right, #924daf, #125bad); */
  /* height: 100vh; */
  background: -webkit-linear-gradient(135deg, #2A2A2A, #97BBEB, #304A4B, #FECACC);
  background: -o-linear-gradient(135deg, #2A2A2A, #97BBEB, #304A4B, #FECACC);
  background: linear-gradient(-45deg, #2A2A2A, #97BBEB, #304A4B, #FECACC);
  background-size: 400% 400%;
  -webkit-animation: gradient 15s ease infinite;
          animation: gradient 15s ease infinite;

  color: white;
  width: 100%;
  height: 100%;
  padding-bottom: 10em;
  padding-top: 5em;
}

@-webkit-keyframes gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

@keyframes gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

p {
  padding: 0;
}

/* .container-card { */
/* margin-top: 100px; */
/* } */

.card-component {
  /* margin-top: 50px; */
  max-width: 100vw;
  height: 70vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.Tilt-inner img:hover {
  -webkit-transition: 500ms ease-in-out;
  -o-transition: 500ms ease-in-out;
  transition: 500ms ease-in-out;
  -webkit-box-shadow: 0px -2px 15px rgb(179, 100, 100), 0 5px 20px rgb(102, 102, 102);
          box-shadow: 0px -2px 15px rgb(179, 100, 100), 0 5px 20px rgb(102, 102, 102);
}

.card-component > * {
  margin-top: 10em;
  /* padding-bottom: 10em; */
}

.card-component img {
  /* border: solid 1px black; */
  max-width: 640px;
  margin-right: 200px;
  border-radius: 10px;
}

/* .imagini-card {
  width: 400px;
  border-radius: 30px;
} */

.text-card {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

a {
  text-decoration: none;
}

a:hover {
  text-decoration: none !important;
  color: #fff;
}

.btn-link1 {
  display: -ms-flexbox;
  display: flex;
  gap: 1em;
}

.btn-link1 > * {
  color: #fff !important;
  border: 4px solid #fff !important;
  background: transparent;
}

.title {
  font-size: 60px;
  justify-self: center;
  -ms-flex-item-align: center;
      align-self: center;
}

.container-work-details {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  width: 30vw;
  /* justify-content: center;
  align-items: center; */
  /* margin-top: 100px; */
}

.work-stack {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  gap: 1em;
  margin-bottom: 1em;
}

.work-description {
  margin-bottom: 1em;
}

@media screen and (max-width: 1420px) {
  .card-component {
    padding: 0;
    width: 100%;
    height: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
  }

  .card-component img {
    margin: auto;
    padding: 0;
    display: block;
    max-width: 90%;
    margin-bottom: 2em;
  }

  .container-work-details {
    width: 80vw;
    margin: auto;
  }

  .text-card {
    padding: 0;
    margin: auto;
  }

}
  

@media (max-width: 600px) {
  .card-component {
    padding: 0;
    /* width: 100%; */
    height: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
  }

  .btn-link1 {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;

    text-align: center;
  }

  /* .btn-link1 > a {
    font-size: 12px !important;
    padding: 8px;
  } */

  .Tilt {
    /* margin-top: 0;
    padding-top: 0; */
    max-width: 100%;
  }

  .card-component img {
    margin: auto;
    padding: 0;
    display: block;
    max-width: 90%;
  }

  .title {
    font-size: 30px;
    padding-bottom: 10px;
  }

  .text-card {
    margin: 0;
  }

  .btn-link1 {
    width: 90%;
    margin: auto;
  }

  .btn-link1 a {
    font-size: 16px;
  }

  .example_d {
    padding: 10px;
  }

  .container-work-details {
    width: 90%;
    height: 100%;
    margin: auto;
  }

  .work-stack {
    -ms-flex-direction: column;
        flex-direction: column;
  }

  .work-stack > * {
    margin: 0;
  }

  .work-description {
    margin: 0;
    margin-bottom: 1em;
  }

}



